    <iPhoneBriefInfo  :isnew="'yes'" :goodname="'iPhone SE'" :goodinfo="''" :buyinfo="' RMB 90/RMB 2149 *'" :priceinfo="'RMB 3299 '" :fontcolor="'black'" :backgoundcolor="'rgb(250, 250, 250)'" :imgsrc="require('./imgs/hero_iphone_se.jpg')"/>
    <iPhoneBriefInfo :reversedirection="'yes'" :goodname="'iPhone11'" :goodinfo="''" :buyinfo="' RMB 167/ RMB 3999 *'" :fontcolor="'black'" :priceinfo="'RMB 3999 '" :backgoundcolor="'rgb(250, 250, 250)'" :imgsrc="require('./imgs/hero_iphone_11.jpg')"/>
