<template>
  <div class="innerappcontainer _inerappcontainer">
    <div>
      <div class="innerapptop  _innerapptop">
        <h1> App</h1>
        <p> Mac  app</p>
      </div>
      <div class="innerappbody _innerappbody">
        <div class="bodyguide">
          <div :class="index===0?'choosed':''" @click="goto(0)">
            <img src="../imgs/nav_icon_photos.jpg" alt="">
            <span></span>
          </div>
          <div :class="index===1?'choosed':''" @click="goto(1)">
            <img src="../imgs/nav_icon_imovie.jpg" alt="">
            <span>iMovie</span>
          </div>
          <div :class="index===2?'choosed':''" @click="goto(2)">
            <img src="../imgs/nav_icon_pages.jpg" alt="">
            <span>Pages</span>
          </div>
          <div :class="index===3?'choosed':''" @click="goto(3)">
            <img src="../imgs/nav_icon_safari.jpg" alt="">
            <span>Safari</span>
          </div>
        </div>
        <div class="infoshow _infoshow">
          <p>{{appinfolist[index]}}</p>
          <img class="macscreeen" src="../imgs/macscreen.png" alt="">
          <img class="screeninfo" :src="appinfoimglist[index]" alt="">
        </div>
      </div>
    </div>
  </div>
</template>

<script>


export default {
  name: '',
  data() {
    return {
      appinfolist:[' iCloud 4',
      ' 4K ',
      ' MaciOS  iPadOS  Microsoft Word ',
      'Safari   Mac Siri  app '
      ],
      appinfoimglist:[
        require('../imgs/bia_photos.jpg'),
        require('../imgs/bia_imovie.jpg'),
        require('../imgs/bia_pages.jpg'),
        require('../imgs/bia_safari.jpg')
      ],
      // 
      index:0,
    }
  },
  methods: {
    goto(i){
      this.index=i
    }
  },
  components: {
  }
}
</script>

<style lang="less" scoped>
// 800px
@media only screen and (min-width: 800px){
    .innerappcontainer{
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 100%;
      div{
        max-width: 1000px;
        width: 100%;
        background-color: rgb(251, 251, 253);
        padding-top: 30px;
        .innerapptop{
            display: flex;
            flex-direction: column;
            align-items: center;
            h1{
              padding-bottom:10px;
            }
            p{
              text-align: center;
            }
          }
        .innerappbody{
          display: flex;
          flex-direction: column;
          align-items: center;
          .bodyguide{
             display: flex;
             flex-direction: row;
             justify-content: space-around;
             width: 95%;
             border-bottom: 1px solid rgba(0, 0, 0, 0.1);
             div{
              width: 10%;
              display: flex;
              flex-direction: column;
              align-items: center;
              padding: 10px 0;
              cursor: pointer;
               img{
                 height: 60px;
               }
               span{
                 font-size: 12px;
               }
             }
          }
          .choosed{
            border-bottom: 2px solid rgba(0, 0, 0, 0.3);
          }
        }
        .infoshow{
          position: relative;
          padding: 30px 50px;
               .macscreeen{
                  width: 470px;
                  margin-top: 50px;
                }
               .screeninfo{
                 position: absolute;
                 bottom: 150px;
                 left: 50%;
                 transform: translateX(-50%);
                 z-index: 999;
                 width: 430px;
               }
        }
      }
    }
}
// 800px
@media only screen and (max-width: 800px){
    ._inerappcontainer{
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 100%;
      div{
        width: 100%;
        background-color: rgb(251, 251, 253);
        padding-top: 30px;
        ._innerapptop{
            display: flex;
            flex-direction: column;
            align-items: center;
            h1{
              padding-bottom:10px;
              font-size: 5.667vw;
            }
            p{
              text-align: center;
              font-size: 2.667vw;
              padding: 2.667vw;
            }
          }
        ._innerappbody{
          display: flex;
          flex-direction: column;
          align-items: center;
          .bodyguide{
             display: flex;
             flex-direction: row;
             justify-content: space-around;
             width: 95%;
             border-bottom: 1px solid rgba(0, 0, 0, 0.1);
             div{
              width: 10%;
              display: flex;
              flex-direction: column;
              align-items: center;
              padding: 10px 0;
              cursor: pointer;
               img{
                 height: 8vw;
               }
               span{
                 font-size: 1.6vw;
               }
             }
          }
          .choosed{
            border-bottom: 2px solid rgba(0, 0, 0, 0.3);
          }
        }
        ._infoshow{
          position: relative;
          padding: 30px 0;
                p{
                   font-size: 2.667vw;
                 }
               .macscreeen{
                  width: 300px;
                  margin-top: 50px;
                }
               .screeninfo{
                 position: absolute;
                 bottom: 110px;
                 left: 50%;
                 transform: translateX(-50%);
                 z-index: 999;
                 width: 280px;

               }
        }
      }
    }
}

</style>


