<script>


export default {
  name: '',
  data() {
    return {
      appinfolist:['Logic Pro  Mac  MIDI ',
      ' Logic Pro X  Mac ',
      'Final Cut Pro  Mac  macOS Catalina ',
      'Motion ',
      ' Final Cut Pro  iTunes Store'
      ],
      appinfoimglist:[
        require('../imgs/pro_logicpro.jpg'),
        require('../imgs/pro_mainstage.jpg'),
        require('../imgs/pro_finalcut.jpg'),
        require('../imgs/pro_motion.jpg'),
        require('../imgs/pro_compressor.jpg')
      ],
      // 
      index:0,
    }
  },
  methods: {
    goto(i){
      this.index=i
    }
  },
  components: {
  }
}
</script>

<style lang="less" scoped>
// 800px
@media only screen and (min-width: 800px){
    .professappcontainer{
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 100%;
      div{
        max-width: 1000px;
        width: 100%;
        background-color: rgb(251, 251, 253);
        padding-top: 30px;
        .innerapptop{
            display: flex;
            flex-direction: column;
            align-items: center;
            h1{
              padding-bottom:10px;
            }
            p{
              text-align: center;
              max-width: 700px;
            }
          }
        .innerappbody{
          display: flex;
          flex-direction: column;
          align-items: center;
          .bodyguide{
             display: flex;
             flex-direction: row;
             justify-content: space-around;
             width: 95%;
             border-bottom: 1px solid rgba(0, 0, 0, 0.1);
             div{
              width: 10%;
              display: flex;
              flex-direction: column;
              align-items: center;
              padding: 10px 0;
              cursor: pointer;
               img{
                 height: 60px;
               }
               span{
                 font-size: 12px;
               }
             }
          }
          .choosed{
            border-bottom: 2px solid rgba(0, 0, 0, 0.3);
          }
        }
        .infoshow{
          position: relative;
          padding: 30px 50px;
               .macscreeen{
                  width: 500px;
                  margin-top: 50px;
                }
               .screeninfo{
                 position: absolute;
                 bottom: 70px;
                 left: 50%;
                 transform: translateX(-50%);
                 z-index: 999;
                 width: 390px;
               }
        }
      }
    }
}
// 800px
@media only screen and (max-width: 800px){
    ._professappcontainer{
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 100%;
      div{
        width: 100%;
        background-color: rgb(251, 251, 253);
        padding-top: 30px;
        .innerapptop{
            display: flex;
            flex-direction: column;
            align-items: center;
            h1{
              padding-bottom:10px;
              font-size: 5.333vw;
            }
            p{
              text-align: center;
              width: 80%;
              font-size: 2.667vw;
            }
          }
        .innerappbody{
          display: flex;
          flex-direction: column;
          align-items: center;
          .bodyguide{
             display: flex;
             flex-direction: row;
             justify-content: space-around;
             width: 95%;
             border-bottom: 1px solid rgba(0, 0, 0, 0.1);
             div{
              width: 15%;
              display: flex;
              flex-direction: column;
              align-items: center;
              padding: 10px 0;
              cursor: pointer;
               img{
                 height: 8vw;
               }
               span{
                 font-size: 1.6vw;
               }
             }
          }
          .choosed{
            border-bottom: 2px solid rgba(0, 0, 0, 0.3);
          }
        }
        .infoshow{
          position: relative;
          // padding: 30px 50px;
                p{
                  font-size: 2.667vw;
                  padding: 10px 20px;
                }
               .macscreeen{
                  width: 300px;
                  margin-top: 50px;
                }
               .screeninfo{
                 position: absolute;
                 bottom: 26px;
                 left: 50%;
                 transform: translateX(-50%);
                 z-index: 999;
                 width:235px;
               }
        }
      }
    }
}

</style>


